const strats = {};
const LIFECYCLE = ['beforeCreate', 'created'];
LIFECYCLE.forEach(hook => {
    // æ¯”å¦‚å°†ç»„ä»¶å’Œmixinçš„createdé’©å­å½¢æˆé˜Ÿåˆ— {created: [fn0, fn1, fn2]}
    // å¦‚æœmixinï¼ˆå„¿å­ï¼‰æœ‰ ç»„ä»¶ï¼ˆçˆ¶äº²ï¼‰æœ‰ åˆ™æ•°ç»„åˆå¹¶
    // å„¿å­æœ‰çˆ¶äº²æ²¡æœ‰ å°†å„¿å­åŒ…æ—¶è£…æˆæ•°ç»„
    // å„¿å­æ²¡æœ‰ çˆ¶äº²ï¼ˆç»„ä»¶é‡Œçš„createdï¼‰åŸå°ä¸åŠ¨å³å¯
    strats[hook] = function(p, c) {
        if(c) {
            if(p) {
                return p.concat(c);
            } else {
                return [c];
            }
        } else {
            return p;
        }
    }
})

strats.components = function(parentVal, childVal) {
    // é€šè¿‡åŸå‹é“¾å¯ä»¥è·å–çˆ¶ç»„ä»¶çš„å…¨éƒ¨components
    const res = Object.create(parentVal);

     // å¦‚æœå­ç»„ä»¶æœ‰componentsé€‰é¡¹ åˆ™é€é¡¹åŠ å…¥ï¼ˆç›¸å½“äºå…ˆåˆå¹¶çˆ¶ç»„ä»¶çš„components å†çœ‹å­ç»„ä»¶ï¼Œæœ‰ç›¸åŒçš„å°±è¦†ç›– æ²¡æœ‰å°±ç›´æ¥åŠ å…¥ï¼‰
    if(childVal) {
        for(let key in childVal) {
            res[key] = childVal[key]
        }
    }
    return res;
}

export function mergeOptions(parent, child) {
    const options = {};
    for(let key in parent) {
        mergeFields(key);
    }
    for(let key in child) {
        if(!parent.hasOwnProperty(key)) {
            mergeFields(key);
        }
    }

    function mergeFields(key) {
        // ç­–ç•¥æ¨¡å¼ï¼ˆå¦‚ç”Ÿå‘½å‘¨æœŸé’©å­ è¦ç‰¹æ®Šå¤„ç† åšæˆé˜Ÿåˆ—ï¼‰
        if(strats[key]) {
            options[key] = strats[key](parent[key], child[key]);
        } else {
            // åˆå¹¶é€‰é¡¹ ä¼˜å…ˆç”¨å­é€‰é¡¹ æ²¡æœ‰åˆ™çˆ¶é€‰é¡¹
            options[key] = child[key] || parent[key];
        }
    }
    return options;
}