// åˆ¤æ–­æ˜¯åŸç”Ÿæ ‡ç­¾è¿˜æ˜¯è‡ªå®šä¹‰ç»„ä»¶
const isReservedTag = (tag) => {
    return ['a', 'div', 'p', 'button', 'ul', 'li', 'span'].includes(tag)
}

// _h() _c() ä¼ å…¥å®ä¾‹ æ ‡ç­¾å æ ‡ç­¾å±æ€§ å­èŠ‚ç‚¹
export function createElementVNode(vm, tag, data, ...children) {
    if(data == null) data = {};
    let key = data.key;
    if(key) {
        delete data.key;
    }
    // å¦‚æœæ˜¯åŸç”Ÿæ ‡ç­¾ç›´æ¥åˆ›å»ºè™šæ‹ŸèŠ‚ç‚¹ å¦åˆ™åˆ›å»ºç»„ä»¶è™šæ‹ŸèŠ‚ç‚¹
    if(isReservedTag(tag)) {
        return vnode(vm, tag, key, data, children);
    } else {
        // è·å–å¯¹åº”çš„ç»„ä»¶å®šä¹‰ å¯èƒ½ä¸ºSubç±» ä¹Ÿå¯èƒ½ä¸ºé€‰é¡¹å¯¹è±¡ï¼ˆcomponents: { {template: 'xxxx'} }ï¼‰
        let Ctor = vm.$options.components[tag];
        return createComponentVnode(vm, tag, key, data, children, Ctor);
    }
}

function createComponentVnode(vm, tag, key, data, children, Ctor) {
    if(typeof Ctor === 'object') {
        // Vue.optionsé»˜è®¤æœ‰_baseå­—æ®µä¿å­˜Vueå…¨å±€æ„é€ å‡½æ•°
        // å¯ä»¥å€Ÿç”¨å®ƒçš„extendé™æ€æ–¹æ³•ç”ŸæˆSubæ„é€ å‡½æ•°
        Ctor = vm.$options._base.extend(Ctor);
    }
    // ç»™ç»„ä»¶è™šæ‹ŸèŠ‚ç‚¹çš„propsæ·»åŠ ä¸€ä¸ªhookå±æ€§
    data.hook = {
        // åˆ›å»ºçœŸå®èŠ‚ç‚¹æ—¶ å¦‚æœæ˜¯ç»„ä»¶åˆ™è°ƒç”¨æ­¤initæ–¹æ³•åˆ›å»º
        // ä¿å­˜ç»„ä»¶çš„å®ä¾‹åˆ°è™šæ‹ŸèŠ‚ç‚¹ä¸Š
        init(vnode) {
            let instance = vnode.componentInstance = new vnode.componentOptions.Ctor;
            instance.$mount();
        }
    }
    return vnode(vm, tag, key, data, children, null, { Ctor })
}

export function createTextVNode(vm, text) {
    return vnode(vm, undefined, undefined, undefined, undefined, text);
}

// astæ˜¯è¯­æ³•å±‚é¢çš„è½¬åŒ– æè¿°çš„æ˜¯è¯­æ³•æœ¬èº« å¯ä»¥æè¿°js css html
// è™šæ‹Ÿdomæè¿°çš„æ˜¯domå…ƒç´  å¯ä»¥ä¸ºdomå¢åŠ ä¸€äº›è‡ªå®šä¹‰å±æ€§
function vnode(vm, tag, key, data, children, text, componentOptions) {
    return {
        vm,
        tag,
        key,
        data,
        children,
        text,
        componentOptions    // å¦‚æœæ˜¯è‡ªå®šä¹‰ç»„ä»¶ä¸æ˜¯åŸç”Ÿæ ‡ç­¾ï¼Œåˆ™componentOptionså­˜æœ‰ç»„ä»¶çš„æ„é€ å‡½æ•°
    }
}

export function isSameVNode(vnode1, vnode2) {
    return vnode1.tag === vnode2.tag && vnode1.key === vnode2.key;
}